.owner__container{
  padding: 10px;
  border:1px dashed #2222;
  border-radius: 10px;
}
.owner__container .owner__icon{
  position: absolute;
  right:-5px;
  top: -5px;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  cursor: pointer;
  background:#eee;
  transition: background .3s ease-in-out;
}
.owner__container .owner__icon:hover{
  background: #ddd;
}