#DropZoneImg {
    margin-bottom: 10px;

	& .image-container {
        width: 94px;
        height: 94px;
        display: flex;
        cursor: pointer;
        position: relative;
        border-radius: 5px;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        border: 2px dashed #D8D6DE;

        &.invalid {
            border-color: #ea5455;
        }

        &:hover {
            border-color: #B9B9C3;
        }

        & svg {
            color: #B9B9C3;
        }

        & .backGroundDropZone {
            font-size: 16px;
            line-height: 20px;
            color: #D8D6DE;
            text-align: center;
            

            & .files {
                font-size: 14px;
                line-height: 21px;         
            }
        }

        & .removeImage-button {
            padding: 5px;
            position: absolute;
            top: -10px;
            right: -10px;
            display: none;
            transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

            &:hover {
                transform : rotate( 180deg );
                box-shadow : 0 0 0 0;
            }

            &.show {
                display : inherit;
            }

            & svg {
                color : #fff;
            }
        }
    }
};