@import "/src/Resources/scss/variables/_variables.scss";
@import "/src/Resources/scss/style.scss";

#UserImage {
    width: fit-content;
    position: relative;
    transition: all .3s ease;


    & .user-avatar.image {
        transition: all .3s ease;
        border-radius: 0.75rem;
        background-color: white;

        &.xs {
            // border: solid 1.5px white;
            box-shadow: 0 3px 10px rgba($secondary, 0.15);
        }

        &.sm {
            // border: solid 2px white;
            box-shadow: 0 4px 12.5px rgba($secondary, 0.125);
        }

        &.mm {
            // border: solid 2.5px white;
            box-shadow: 0 5px 15px rgba($secondary, 0.1);
        }

        &.lg {
            // border: solid 3px white;
            box-shadow: 0 7.5px 20px rgba($secondary, 0.075);
        }

        &.xl {
            // border: solid 4px white;
            box-shadow: 0 10px 22.5px rgba($secondary, 0.05);
        }

        &.rounded {
            border-radius: 50% !important;

            & img {
                border-radius: 50% !important;
            }
        }

        & img {
            border-radius: 0.75rem;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    & .user-avatar.initials {
        transition: all .3s ease;
        border-radius: 0.75rem;
        background-color: white;

        &.xs {
            // border: solid 1.5px white;
            box-shadow: 0 3px 10px rgba($primary, 0.2);

            & span {
                font-size: 6px;
                font-weight: 500;
            }
        }

        &.sm {
            // border: solid 2px white;
            box-shadow: 0 4px 12.5px rgba($primary, 0.175);
            
            & span {
                font-size: 9px;
                font-weight: 500;
            }
        }

        &.mm {
            // border: solid 2.5px white;
            box-shadow: 0 5px 15px rgba($primary, 0.15);
            
            & span {
                font-size: 12px;
                font-weight: 500;
            }
        }

        &.lg {
            // border: solid 3px white;
            box-shadow: 0 7.5px 20px rgba($primary, 0.125);

            & span {
                font-size: 15px;
                font-weight: 500;
            }
        }

        &.xl {
            // border: solid 4px white;
            box-shadow: 0 10px 22.5px rgba($primary, 0.1);
            
            & span {
                font-size: 18px;
                font-weight: 500;
            }
        }

        &.rounded {
            border-radius: 50% !important;

            & span {
                border-radius: 50% !important;
            }
        }

        & span {
            border-radius: 0.75rem !important;
            color: $primary;
            background-color: rgba($primary, 0.15);
            width: 100%;
            height: 100%;
        }
    }

    & .subIcon-container {
        position: absolute;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        right: -0.5px;
        top: -0.5px;
        border: solid 2px white;
        background-color: white;

        & .background {
            position: absolute;
            border-radius: 50%;
            width: 100%;
            z-index: 1;
            height: 100%;
            // opacity: 0.15;
        }

        & span {
            width: 12.5px !important;
            height: 12.5px !important;
            display: flex;
            align-items: center;
            justify-content: center;

            & svg {
                width: 100% !important;
                height: 100% !important;
            }
        }
    } 
}