#FormSelectGroup {
    // margin-bottom: 0 !important;
    .text-error {
        font-size: 0.8rem;
        color: #f44336;
        font-weight: 500;
    }
    
    .react-select {
        .select__control {
            border-color: #d8d6de;
        }
    
        .select__placeholder {
            color: #b9b9c3;
        }
    
        &.errorSelect {
            & .css-1s2u09g-control {
                border-color: #f44336;
                border-radius: 4px !important;
    
                & svg {
                    color : #f44336;
                }
            }
        }
    }
}