#FormTreePicker {
    margin-bottom: 1rem !important;

    .text-error {
        font-size: 0.8rem;
        color: #f44336;
        font-weight: 500;
    }
    
    .invalid {
        .rs-picker-default {
            border-color: red !important;
        }

        .rs-picker-toggle {
            border-color: red !important;
        }
    }
}