@import "/src/Resources/scss/variables/variables";

#Menu {
    display: flex;
    gap: 1rem;
    padding: 3rem;
    flex-direction: column;
    justify-content: center;

    .link {
        &.active {
            font-size: 16px;
            color: darken($primary, 10%)
        }
        &:hover {
            color : lighten($primary, 10%);
        }
    }
}