@import "/src/Resources/scss/variables/_variables.scss";

#Enviroment {
    .react-dropdown-tree-select {
        width: 100%;
        .dropdown-trigger {
            // border: 1px solid red !important;
            border-radius: 5px !important;
            min-width: 100% !important;
            width: 100% !important;
            
        }
        li {
            &.node {

                &.disabled {
                    cursor: pointer;
                    i { 
                        position: absolute; width: 100%; text-align: left; 
                    }
                } 
            }

        }
    }
    .react-dropdown-tree-select li.node.disabled input[type="radio"] { visibility: hidden; }

    .node.disabled >* {
        cursor: pointer !important;
    }

    .form-control[readonly] {
        opacity: 1;
    }

    .form-control[readonly] {
        background-color: #fff;
    }

    .tag-item .search {
        border: none;
        border-bottom: none !important;
    }
}