#ConfirmationModal {
    padding: 20px;

    .wrapper {
        gap : 20px;
        display: flex;
        align-items: center;
        flex-direction: column;

        .title {
            font-weight: 500;
            font-size: 25px;
            line-height: 30px;
            text-align: center;
            color: #2C2C26;
        }
        .text-wrapper {
            display: flex;
            align-items: center;
            flex-direction: column;

            .text {
                font-size: 15px;
                line-height: 18px;

                &.txt-body {
                    text-align: center;
                    max-width: 350px;
                }
            }
        }
    }
}