#UploadSingleFile { 
    margin-bottom: 15px;
    
    .label {
        font-size: 13px;
        line-height: 15px;
        color: #3F3F3F;
        margin-bottom: 10px;
    }
    
    .file-uploaded {
        border-radius: 6px;
        background-color: #f8f8f8 !important;
        border: 2px solid rgb(97, 97, 97) !important;
    }

    .card {
        width : 100%;
        height : 100%;

        .card-body {
            display: flex;
            gap: 5px;
            overflow: hidden;
            align-items: center;

            p {
                width: 90%;
                overflow: hidden;
                white-space: nowrap;
            }

            .text-name {
                font-weight: 600;
                font-size: 12px;
                color: #5E5873;
                line-height: 15px;
                text-overflow: ellipsis;
                text-transform: uppercase;
            }
        }
    }
    
    .show-file {
        z-index: 5;
        top: -10px;
        right: 25px;
        padding: 5px;
        display: none;
        position: absolute;
        transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

        &:hover {
            box-shadow : 0 0 0 0;
            transform : rotate( 180deg );
        
        }
        
        &.show {
            display : inherit;
        }

        svg {
            color : #fff;
        }
    }
    
    .file-container {        
        width: 100%;
        height: 100%;
        cursor: pointer;
        position: relative;
        border-radius: 6px;
        border: 2px dashed #D8D6DE;      

        &.content-format-not-file {
            padding: 20px;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
        }

        &.invalid {
            border-color: #ea5455;
        }

        &:hover {
            border-color: #B9B9C3;
        }

        & svg {
            color: #B9B9C3;
        }


        .text {
            font-size: 12px;
            line-height: 14px;
            text-align: center;
            color: #B9B9C3;

            &.invalid {
                color :#ea5455;
            }

            .files {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 21px;
            }
        }
        
        .removeImage-button {
            z-index: 2;
            top: -10px;
            right: -10px;
            padding: 5px;
            display: none;
            position: absolute;
            transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

            &:hover {
                box-shadow : 0 0 0 0;
                transform : rotate( 180deg );
            
            }
            
            &.show {
                display : inherit;
            }

            svg {
                color : #fff;
            }
        }
    }
}