#FormInputGroup {
    margin-bottom: 0 !important;

    .mb-3 {
        margin-bottom: 0 !important;
    }

    .form-group-style {
        width: 100%;
    }
    
    .text-error {
        font-size: 0.8rem;
        color: #f44336;
        font-weight: 500;
    }

    .invalid {
        color: #f44336 !important;
        margin-bottom: 0!important;
        border-color: #f44336 !important;

        svg {
            color: #f44336 !important;
        }
    }

    .form-control.is-invalid {
        z-index: 0 !important;
    }

}