@import "/src/Resources/scss/variables/_variables.scss";

#Comments {
    margin-top: 1rem;
    margin-bottom: 2rem;

    .end-message {
        text-align: center;
        margin-top: 5px;
    }

    .comments-btns-wrapper {
        display: flex;
        gap: 1rem;
        align-items: center;
        margin-top: 1rem;
    }

    .massage-content {
        display: flex;
        gap : 1rem;

        .user-name {
            color: $primary;
            font-size: 13px;
            font-weight: bold;
            white-space: nowrap;
            line-height: 1.8rem;
        }

        .date {
            margin-left: 5px;
            color: #b9b9c3;
            font-size: 12px;
        }
    }

}