@import "/src/Resources/scss/variables/variables";

#AppraisalTable {
    .pending {
        text-align: center;
        .text {
            color: #FF784F;

        }
        &:hover {
            .text{
                transform: scale(1.1);
                color : lighten(#FF784F, 10%);
            }
        }
        p {
            margin: 0 !important;
        }
    }
    .deparment {
        display: flex;
        align-items: center;
        justify-content: center;
        gap : 5px;
        svg {
            color : $primary;
        }

        p{
            font-family: 'Rubik';
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 23px;
            color: #5E5873;
        }
    }

    .actions-wrapper {
        display: flex;
        align-items: center;
        justify-content: end;
        gap : 5px;
    }
}